import React, {useState, useContext} from 'react';
import { QuizContext } from '../Helpers/Contexts'
import { Questions } from '../Helpers/QuestionBank';
import '../App.css';


function Quiz() {

    const { score, setScore, setGameState, currQuestion, setCurrQuestion, optionChosen, setOptionChosen } = useContext(QuizContext);

    const nextPerformance = () => {
       if (Questions[currQuestion].answer === optionChosen) {
            setScore(score + 1);
        }  
        setGameState("performance");
    };

    return (
        <div className="Quiz">
            <p>Pergunta {currQuestion + 1} de {Questions.length}</p>
            <h1>{Questions[currQuestion].prompt}</h1>
            <div className="options">
                <button onClick={() => setOptionChosen('A' || 'D')}>{Questions[currQuestion].optionA}</button>
                <button onClick={() => setOptionChosen('B' || 'E')}>{Questions[currQuestion].optionB}</button>
                <button onClick={() => setOptionChosen('C' || 'F')}>{Questions[currQuestion].optionC}</button>
            </div>

            <button className="btn-next" onClick={nextPerformance}>Seguinte</button>
            
            
        </div>
    );
}

export default Quiz;