import React, {useState, useContext} from 'react';
import ProgressBar from 'react-animated-progress-bar';
import { QuizContext } from '../Helpers/Contexts'
import { Questions } from '../Helpers/QuestionBank';
import '../App.css';


function Performance1() {

    const { score, setScore, setGameState, optionChosen, currQuestion, setCurrQuestion } = useContext(QuizContext);

    const nextQuestion = () => {
        if (Questions[currQuestion].answer === optionChosen) {
            setScore(score + 1);
        } 
        setCurrQuestion(currQuestion + 1);
        setGameState("quiz");
    };

    const finishQuiz = () => {
        if (Questions[currQuestion].answer === optionChosen) {
            setScore(score + 1);
        }
        setGameState("endScreen");
    };
    return (
        <div className="Performance">
            <h2>Performance</h2>
            <p>Impacto da Solução {optionChosen}</p>
            <div className="Stakeholders">
                Accionistas<ProgressBar
                    width="400px"
                    height="15px"
                    rect="true"
                    fontColor="black"
                    percentage="70"
                    rectPadding="2px"
                    rectBorderRadius="15px"
                    trackPathColor="transparent"
                    trackBorderColor="white"
                />
                Comunidade<ProgressBar
                    width="400px"
                    height="15px"
                    rect="true"
                    fontColor="black"
                    percentage="10"
                    rectPadding="2px"
                    rectBorderRadius="15px"
                    trackPathColor="transparent"
                    trackBorderColor="white"
                />
                ONG's<ProgressBar
                    width="400px"
                    height="15px"
                    rect="true"
                    fontColor="black"
                    percentage="50"
                    rectPadding="2px"
                    rectBorderRadius="15px"
                    trackPathColor="transparent"
                    trackBorderColor="white"
                />
                Colaboradores<ProgressBar
                    width="400px"
                    height="15px"
                    rect="true"
                    fontColor="black"
                    percentage="20"
                    rectPadding="2px"
                    rectBorderRadius="15px"
                    trackPathColor="transparent"
                    trackBorderColor="white"
                />
                Governo<ProgressBar
                    width="400px"
                    height="15px"
                    rect="true"
                    fontColor="black"
                    percentage="80"
                    rectPadding="2px"
                    rectBorderRadius="15px"
                    trackPathColor="transparent"
                    trackBorderColor="white"
                />
                Fornecedores<ProgressBar
                    width="400px"
                    height="15px"
                    rect="true"
                    fontColor="black"
                    percentage="100"
                    rectPadding="2px"
                    rectBorderRadius="15px"
                    trackPathColor="transparent"
                    trackBorderColor="white"
                />
                Clientes<ProgressBar
                    width="400px"
                    height="15px"
                    rect="true"
                    fontColor="black"
                    percentage="40"
                    rectPadding="2px"
                    rectBorderRadius="15px"
                    trackPathColor="transparent"
                    trackBorderColor="white"
                />
            </div>
             {currQuestion === Questions.length - 1 ? (
                <button className="btn-finish" onClick={finishQuiz}>Terminar Quiz</button>
            ) : (
                <button className="btn-next" onClick={nextQuestion}>Seguinte</button>
            )}
        </div>
    );
}

export default Performance1;