import React, {useContext} from 'react';
import { QuizContext } from '../Helpers/Contexts';
import '../App.css';


function MainMenu() {
    const {gameState, setGameState} = useContext(QuizContext);
    
    return (
        <div className="Menu">
            <h1>Quiz de Sustentabilidade</h1>
            <h2>Bem-vindo!</h2>
            <div className="menu-text">
                <p>Este pequeno questionário tem como objectivo mostrar que as decisões que uma empresa toma têm impactos que podem não ser consensuais para os diversos stakeholders (partes interessadas), com os quais se relaciona.</p>
                <p>Após a escolha de uma opção em cada uma das perguntas, terá como feedback quais os resultados que a sua opção teve nos diferentes stakeholders. Não existem respostas certas nem erradas, mas sim opções com diferentes repercussões, consoante os interesses do stakeholder.</p>
                <p>Está preparado?</p>
            </div>
            <button className="btn-start" onClick={() => {setGameState("quiz")}}>
            Começar
            </button>
        </div>
    );
}

export default MainMenu;