import React, { useState } from 'react';
import MainMenu from './Components/MainMenu';
import Quiz from './Components/Quiz';
import EndScreen from './Components/EndScreen';
import './App.css';
import AliceCarousel from 'react-alice-carousel';
import 'react-alice-carousel/lib/alice-carousel.css';
import image1 from './Images/img1.jpg';
import image2 from './Images/img2.jpg';
import image3 from './Images/img3.jpg';
import image4 from './Images/img4.jpg';
import image5 from './Images/img5.jpg';
import image6 from './Images/img6.jpg';
import image7 from './Images/img7.jpg';
import Performance1 from './Components/Performance1';
import {QuizContext} from './Helpers/Contexts';

function App() {
  const [gameState, setGameState] = useState("menu");
  const [score, setScore] = useState(0);
  const [currQuestion, setCurrQuestion] = useState(0);
  const [optionChosen, setOptionChosen] = useState("");

  return (
     
    <div className="App">
      <AliceCarousel autoPlay autoPlayInterval="3000" infinite disableButtonsControls disableDotsControls animationEasingFunction>
        <img src={image1} className="sliderimg"/>
        <img src={image2} className="sliderimg"/>
        <img src={image3} className="sliderimg"/>
        <img src={image4} className="sliderimg"/>
        <img src={image5} className="sliderimg"/>
        <img src={image6} className="sliderimg"/>
        <img src={image7} className="sliderimg"/>
      </AliceCarousel>
           
      <QuizContext.Provider value={{ gameState, setGameState, score, setScore, currQuestion, setCurrQuestion, optionChosen, setOptionChosen}}>
        {gameState === "menu" && <MainMenu />}
        {gameState === "quiz" && <Quiz />}
        {gameState === "performance" && <Performance1 />}
        {gameState === "endScreen" && <EndScreen />}
      </QuizContext.Provider>
    </div>
  );
}

export default App;
