import React, {useContext} from 'react';
import { QuizContext } from '../Helpers/Contexts';
import { Questions } from '../Helpers/QuestionBank'
import '../App.css';

function EndScreen() {
    const { score, setScore, setCurrQuestion, setGameState } = useContext(QuizContext);

    const restartQuiz = () => {
        setScore(0);
        setCurrQuestion(0);
        setGameState("menu");
    };

    return (
        <div className="EndScreen">
            <h1>Parabéns, chegou ao fim!</h1>
            <p>Resposta positiva pelos bons resultados.</p>
            <button className="btn-again" onClick={restartQuiz}>Tentar de novo?</button>
        </div>
    )
}

export default EndScreen;